import { CBadge, CCard, CCardBody, CCol, CDataTable } from '@coreui/react';
import React from 'react';
import { Link } from 'react-router-dom';
import { url } from 'src/helpers/helpers';
import {toast,ToastContainer} from 'react-toastify'
import {userContext} from '../../context/UserContext'
import CustomModal from 'src/components/CustomModal';


const getBadge = status => {
    switch (status) {
      case '1': return 'success'
      case '2': return 'secondary'
      // case 'Pending': return 'warning'
      // case 'Banned': return 'danger'
      default: return 'primary'
    }
  }

export default function Index(){
    const {user,setLoad} = React.useContext(userContext);
    const fields = ['#','car_name','serial_number','plate_number','client_name','action'];
    const [vehicleList,setVehicleList] = React.useState([]);
    const [id,setId] = React.useState(null);
    const [modal,setModal] = React.useState(false)

    React.useEffect(()=>{
      setLoad(true)
      async function fetchData(){
        const response = await fetch(url + 'vehiclelist',{
          method : 'GET',
          headers : {
            'Authorization' : user?.token
          }
        })

        if (response.ok === true){
          const data = await response.json();
          setLoad(false)
          if (data.status === 200){
            // deleteProduct
            setVehicleList(data.vehicle_list.map((item,index)=>{
              return {
                '#' : index +1,
                'car_name' : item.car_name,
                'serial_number' : item.serial_number,
                'plate_number' : item.plate_number,
                'client_name' : item.client_name,
                'vehicle_id' : item.id
              }
            }));
          }
        }
      }
      fetchData();
    },[])

    const deleteEntry = () => {
      setLoad(true)
      async function deleteData(){
        const response = await fetch(url + 'deletevehicle/' + id,{
          method : 'GET',
          headers : {
            'Authorization' : user?.token
          }
        })

        const data = await response.json();
        setLoad(false)
        if (data.status === 200){
          setModal(false);
          setId('');
          async function fetchData(){
            const response = await fetch(url + 'vehiclelist',{
              method : 'GET',
              headers : {
                'Authorization' : user?.token
              }
            })
    
            if (response.ok === true){
              const data = await response.json();
    
              if (data.status === 200){
                // deleteProduct
                setVehicleList(data.vehicle_list.map((item,index)=>{
                  return {
                    '#' : index +1,
                    'car_name' : item.car_name,
                    'serial_number' : item.serial_number,
                    'plate_number' : item.plate_number,
                    'client_name' : item.client_name,
                    'vehicle_id' : item.id
                  }
                }));
              }
            }
          }
          fetchData();
          
        }else{
          toast.error(data.message)
        }
      }
      deleteData();
    }

    const showModal = value => {
      setId(value);
      setModal(true)
    }

    return(
        <section>
          <ToastContainer />
          <CustomModal modal={modal} setModal={setModal} deleteEntry={deleteEntry} />
            <Link to='/create/vehicle'>Create Vehicle</Link>
            <CCol xs="12" lg="12">
          <CCard>
            <CCardBody>
            <CDataTable
              items={vehicleList}
              fields={fields}
              columnFilter
              tableFilter
              itemsPerPageSelect
              itemsPerPage={5}
              hover
              sorter
              pagination
              scopedSlots = {{
                'status':
                  (item)=>(
                    <td>
                      <CBadge color={getBadge(item.status_id)}>
                        {item.status_id === 1 ? 'Active' : 'Inactive'}
                      </CBadge>
                    </td>
                  ),
                'action': (item)=>(
                  <td>
                    <Link to={`/edit/vehicle/${item.vehicle_id}`}><i class="fa fa-pencil" aria-hidden="true"></i></Link>
                    <i style={{cursor:"pointer"}} onClick={()=>showModal(item.vehicle_id)} class="fa fa-trash" aria-hidden="true"></i>
                  </td>
                )

              }
            }
            />
            </CCardBody>
          </CCard>
        </CCol>
        </section>
    )
}